<template>
  <v-card max-width="550" max-height="700">
    <v-carousel
      v-model="carouselModel"
      :cycle="post.images.length > 1"
      hide-delimiter-background
      hide-delimiters
      :show-arrows="post.images.length > 1"
    >
      <v-carousel-item
        v-for="(image, index) in post.images"
        :key="'postN' + index"
        :src="image"
      ></v-carousel-item>
    </v-carousel>
    <v-card-title>
      {{ post.title }}
    </v-card-title>
    <v-card-text>
      {{ post.description }}
    </v-card-text>
    <div v-if="post.link">
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            class="positioned-button"
            icon
            @click="redirectToPost(post.link)"
            v-bind="attrs"
            v-on="on"
          >
            <v-icon color="pink">mdi-instagram</v-icon>
          </v-btn>
        </template>
        <span>Visit original post</span>
      </v-tooltip>
    </div>
  </v-card>
</template>

<script>
export default {
  props: {
    post: {
      type: Object,
      default: Object,
      required: true,
    },
  },
  data() {
    return {
      carouselModel: 0,
    };
  },
  methods: {
    redirectToPost(url) {
      window.open(url, "_blank");
    },
  },
};
</script>

<style lang="scss" scoped>
.positioned-button {
  position: absolute;
  bottom: 5%;
  right: 5%;
}
</style>
